<template>
   <v-layout row wrap>
      <v-flex sm12>
         <v-data-table :headers="headers" :items="ticks" hide-actions>
            <template slot="headerCell" slot-scope="{ header }">
               <span class="subheading font-weight-light text-success text--darken-3 cn">
                  {{ header.text }}
               </span>
            </template>
            <template slot="items" slot-scope="props">
               <td class="cn">{{ props.item.symbol }}</td>
               <td>{{ props.item.time }}</td>
               <td>{{ props.item.price }}</td>
               <td :style="getStyle(props.item)">{{ props.item.qty }}</td>
               <td>{{ props.item.bid }}</td>
               <td>{{ props.item.offer }}</td>
            </template>
         </v-data-table>
      </v-flex>
      <v-flex sm12 class="cn">
         (Tick). 
         <ul>
            <li>
               Bid/Offer 0 
            </li>
            <li>
               Offer <span style="color:red">1</span>  
            </li>
            <li>
               Bid <span style="color:green">-1</span>  
            </li>
         </ul>
      </v-flex>
      <v-flex sm12 class="cn">
         
         <ul>
            <li>
               10:30:00 ~ 10:31:00  ? 1. 612 
            </li>
            <li>
               10:30:00 ~ 10:31:00  ? 1. 398 
            </li>
            <li>
               11:12:00 ~ 11:13:00  ? -1. 737 
            </li>
            <li>
               11:12:00 ~ 11:13:00  ? -1. 515 
            </li>
         </ul>
      </v-flex>
   </v-layout>
</template>

<script>
export default {
   data () {
		return {
			headers: [
				{
					sortable: false,
					text: '',
					value: 'symbol'
				},
				{
					sortable: false,
					text: '',
					value: 'time'
				},
				{
					sortable: false,
					text: '',
					value: 'price'
            },
            {
					sortable: false,
					text: '',
					value: 'qty'
				},
				{
					sortable: false,
					text: 'Bid',
					value: 'bid'
				},
				{
					sortable: false,
					text: 'Offer',
					value: 'offer'
				}
         ],
         ticks: [{
            symbol: '',
            time: '10:21:11',
            price: 9767,
            qty: 3,
            bid: 9766,
            offer: 9768
         },{
            symbol: '',
            time: '10:21:10',
            price: 210.5,
            qty: 46,
            bid: 210,
            offer: 210.5
         },{
            symbol: '',
            time: '10:21:10',
            price: 22.1,
            qty: 190,
            bid: 22.1,
            offer: 22.15
         }]
			
		}
   },
   methods: {
		getStyle(item){
         if(item.price <= item.bid) return 'color:green';
         else if(item.price >= item.offer) return 'color:red';
         else return '';
		}
	}
}
</script>
